        // 页面刷新
        reload(){
            // 页面属性不白屏 数据驱动
            this.isRouterAlive = false
            this.$nextTick(() =>{
                this.isRouterAlive = true
                // location.reload() //不要使用这种方法
                const reload = document.getElementById('reload');
                let speed = 0;
                const timer = setInterval(() =>{
                    speed += 2
                    if(speed >= 360){
                        clearInterval(timer);
                        speed = 360
                    }
                    reload.style.transform = `rotate(${speed}deg)`;
                },1)
            })
        },
