<template>
    <div class="home-wrapper">
        <div class="home-box">
            <div class="home-left">
                <div class="test-paper">
                    <p class="test-title">
                        考生试卷情况
                    </p>
                    <div class="test-paper-content">
                        <ul>
                            <li v-for="(mess,i) in testPaper" :key="i">
                                <div class="icon">
                                    <i v-html="mess.icon" class="iconfont">{{mess.icon}}</i>
                                </div>
                                <div class="mess-desc">
                                    <p class="num">{{mess.num}}</p>
                                    <p class="desc-name">{{mess.name}}</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="ratio">
                    <ul class="ratio-list"> 
                        <li class="chart">
                            <p class="ratio-title">占比情况</p>
                            <div id="myChart1"></div>
                            <ul>
                                <li class="color" v-for="(item,index) in ratioMess" :key="index">
                                    <div class="circle" :style="{background: item.color}"></div>
                                    <span>{{item.name}}</span>
                                </li>
                            </ul>
                        </li>
                        <li class="chart">
                            <p class="ratio-title">试题难度选择情况</p>
                            <div id="myChart2"></div>
                            <ul>
                                <li class="color" v-for="(item,index) in parperSelect" :key="index">
                                    <div class="circle" :style="{background: item.color}"></div>
                                    <span>{{item.name}}</span>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="home-right">
                <div class="right-top">
                    <div class="calendar">
                        <p class="calendar-name">日历</p>
                        <el-calendar v-model="value"></el-calendar>
                    </div>
                    <div class="task">
                        <ul class="task-list">
                            <li>
                                <div class="icon">
                                    <i class="iconfont">&#xe653;</i>
                                </div>
                                <div class="task-desc">
                                    <p class="task-time">09：30AM - 11:30AM</p>
                                    <p class="task-content">A卷考试，分配工作任务</p>
                                </div>
                            </li>
                            <li>
                                <div class="icon">
                                    <i class="iconfont">&#xe653;</i>
                                </div>
                                <div class="task-desc">
                                    <p class="task-time">01：30PM - 06:30PM</p>
                                    <p class="task-content">B卷考试，分配工作任务</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="right-bottom">
                    <div class="log">
                        <p class="log-title">登录日志</p>
                        <div class="log-content">
                            <div class="icon">
                                <i class="iconfont">&#xe610;</i>
                            </div>
                            <div class="log-desc">
                                <p class="log-time">2019-02-20<span>07:30PM</span> </p>
                                <p class="log-mess">系统登录成功，IP地址：192.168.1.106</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style scoped>
    @import './home.css';
</style>

<script>
export default {
    data(){
        return{
            testPaper: [
                {
                    name: '未合格',
                    icon: '&#xe62a;',
                    num: 32
                },
                {
                    name: '合格',
                    icon: '&#xe638;',
                    num: 108
                },
                {
                    name: '缺考人数',
                    icon: '&#xe620;',
                    num: 240
                },
                {
                    name: '总出题',
                    icon: '&#xe612;',
                    num: 190
                }
            ],
            ratioMess: [
                {
                    ratio: '18%',
                    name: '未合格',
                    color: '#ff637b'
                },
                {
                    ratio: '20%',
                    name: '合格',
                    color: '#4f70f0'
                },
                {
                    ratio: '14%',
                    name: '人数',
                    color: '#ff8572'
                },
                {
                    ratio: '48%',
                    name: '总出题',
                    color: '#00c1ce'
                }
            ],
            parperSelect: [
                {
                    ratio: '40%',
                    name: '一般',
                    color: '#ff637b'
                },
                {
                    ratio: '30%',
                    name: '较难',
                    color: '#4f70f0'
                },
                {
                    ratio: '20%',
                    name: '普通难度',
                    color: '#ff8572'
                },
                {
                    ratio: '10%',
                    name: '最高难度',
                    color: '#00c1ce'
                }
            ],
            value: new Date()
        }
    },
    methods: {
         _drawLine1(){
        // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById('myChart1'))
            const data = []
            this.ratioMess.forEach((ele,index) =>{
                data[index] = {}
                data[index]['value'] = parseInt(ele.ratio);
                data[index]['name'] = ele.name + ele.ratio;
                data[index]['itemStyle'] = {
                    color: ele.color, 
                    emphasis: {
                                // shadowBlur: 200,
                                shadowColor: 'rgba(0, 0, 0, 0.7)'
                            }}
            })
            data.reverse() //逆序
            const option = {
                textStyle: {
                    "fontSize": 14
                },
                series : [
                    {
                        name: '访问来源',
                        type: 'pie',
                        radius: '55%',
                        data
                    }
                ]
            };
            myChart.setOption(option);
        },
         _drawLine2(){
        // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById('myChart2'))
            const data = []
            this.parperSelect.forEach((ele,index) =>{
                data[index] = {}
                data[index]['value'] = parseInt(ele.ratio);
                data[index]['name'] = ele.name + ele.ratio;
                data[index]['itemStyle'] = {color: ele.color}
            })
            data.reverse()//逆序
            const option = {
                textStyle: {
                    "fontSize": 14
                },
                series : [
                    {
                        name: '访问来源',
                        type: 'pie',
                        radius: '55%',
                        data
                    }
                ]
            };
            myChart.setOption(option);
        },
        
    },
    mounted(){
        // 确保dom挂载好再执行
        this._drawLine1();
        this._drawLine2()
    }
}
</script>