<template>
    <div class="set-wrapper">
        <div class="issue-box">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="创建附件" prop="adjunct">
                    <el-select v-model="ruleForm.adjunct" filterable allow-create default-first-option
                        placeholder="请选择或创建附件">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="标题" prop="title">
                    <el-input v-model="ruleForm.title" placeholder="输入标题"></el-input>
                </el-form-item>
                <!-- 编辑器 -->
                <div class="editor">
                    <el-form-item label="考试大纲">
                        <!-- <el-input type="textarea" v-model="ruleForm.pandect" placeholder="输入标题"></el-input> -->
                        <z-editor :id="tinymce" :tinymceHtml='tinymceHtml'></z-editor>
                     </el-form-item>
                </div> 
                <el-form-item label="考试时长" prop="time">
                    <el-input type="number" v-model="ruleForm.time" placeholder="请输入考试时长(分钟)"></el-input>
                </el-form-item>                
                <el-form-item label="内容文本" prop="content">
                    <el-input type="textarea" v-model="ruleForm.content" placeholder="请输入题目详解"></el-input>
                </el-form-item>
                
                <el-form-item label="题目类型" prop="topicType">
                    <template>
                        <el-select v-model="ruleForm.topicType">
                            <el-option
                                v-for="item in options2"
                                :key="item.value"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </template>
                </el-form-item>
                <el-form-item label="难度" prop="degree">
                    <template>
                        <el-select v-model="ruleForm.degree">
                            <el-option
                                v-for="item in degree" 
                                :key="item.value"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </template>
                </el-form-item>
                
                <el-form-item label="开始出题" prop="topic">
                    <el-input type="textarea" v-model="ruleForm.topic" placeholder="请出题"></el-input>
                </el-form-item>
                
                <div class="option" v-if="ruleForm.topicType != '填空题'">
                    <el-form-item label="A：" prop="optionA">
                        <el-input v-model="ruleForm.optionA"></el-input>
                    </el-form-item>
                    <el-form-item label="B：" prop="optionB">
                        <el-input v-model="ruleForm.optionB"></el-input>
                    </el-form-item>
                    <el-form-item label="C：" prop="optionC">
                        <el-input v-model="ruleForm.optionC"></el-input>
                    </el-form-item>
                    <el-form-item label="D：" prop="optionD">
                        <el-input v-model="ruleForm.optionD"></el-input>
                    </el-form-item>
                    <el-form-item v-if="ruleForm.topicType == '单选题'" label="E：" prop="optionE">
                        <el-input v-model="ruleForm.optionE"></el-input>
                    </el-form-item>
                    <el-form-item v-if="ruleForm.topicType == '单选题'" label="F：" prop="optionF">
                        <el-input v-model="ruleForm.optionF"></el-input>
                    </el-form-item>
                </div>
                <div class="answer">
                    <el-form-item v-if="ruleForm.topicType == '单选题'" label="正确答案" prop="correct">
                        <el-radio v-model="ruleForm.correct" label="A">A</el-radio>
                        <el-radio v-model="ruleForm.correct" label="B">B</el-radio>
                        <el-radio v-model="ruleForm.correct" label="C">C</el-radio>
                        <el-radio v-model="ruleForm.correct" label="D">D</el-radio>
                        <el-radio v-model="ruleForm.correct" label="E">E</el-radio>
                        <el-radio v-model="ruleForm.correct" label="F">F</el-radio>
                        <el-checkbox-group v-if="ruleForm.topicType == '多选题'" v-model="ruleForm.checkList">
                            <el-checkbox label="A"></el-checkbox>
                            <el-checkbox label="B"></el-checkbox>
                            <el-checkbox label="C"></el-checkbox>
                            <el-checkbox label="D"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item v-if="ruleForm.topicType == '多选题'" label="正确答案" prop="checkList">
                        <el-checkbox-group v-model="ruleForm.checkList">
                            <el-checkbox label="A"></el-checkbox>
                            <el-checkbox label="B"></el-checkbox>
                            <el-checkbox label="C"></el-checkbox>
                            <el-checkbox label="D"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item v-if="ruleForm.topicType == '填空题'" label="正确答案" prop="gap">
                        <el-input v-model="ruleForm.gap"></el-input>
                    </el-form-item>
                </div>
                <div class="grade">
                    <el-form-item label="分数" prop="grade">
                            <el-input type="number" v-model="ruleForm.grade"></el-input>
                    </el-form-item>
                </div>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">添加至{{ruleForm.adjunct|| '附件'}}</el-button>
                </el-form-item>
                 <!-- 最后确认对话框 -->
                <el-dialog title="考试时间" :visible.sync="dialogFormVisible">
                    <el-form-item label="考试日期" prop="date">
                        <div class="block">
                            <el-date-picker
                                v-model="ruleForm.date"
                                type="datetime"
                                placeholder="请填写开始考试日期">
                            </el-date-picker>
                        </div>
                    </el-form-item>
                    <el-form-item label="审核" prop="way">
                            <el-select v-model="ruleForm.way" placeholder="请选择阅卷方式">
                                <el-option
                                    v-for="item in ways" 
                                    :key="item.value"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                    </el-form-item>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click="submitTure">提 交</el-button>
                    </div>
                </el-dialog>
            </el-form>
        </div>
       
        <!-- <el-dialog
            title="考试时间"
            :visible.sync="dialogVisible"
            width="30%">
            <span>这是一段信息</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitTure">确 定</el-button>
            </span>
        </el-dialog> -->
    </div>
</template>

<style scoped>
    @import './setTopic.css';
</style>
<script>
import Editor from '../Editor'
import tinymce from 'tinymce/tinymce'
export default {
    name: '',
    components: {
        "z-editor": Editor
    },
    data(){
        return {
            tinymce: 'tinymce',
            tinymceHtml: '请输入内容',
            dialogFormVisible: false,
            // 附件
            options: [
                        {
                        value: '附件1'
                        }, {
                        value: '附件2'
                        }, {
                        value: '附件3'
                        }
                ],
            options2: [
                        {
                        value: '单选题'
                        }, {
                        value: '多选题'
                        }, {
                        value: '填空题'
                        }
                ],
            degree: [
                        {
                        value: '一般'
                        }, {
                        value: '普通难度'
                        }, {
                        value: '较难'
                        },{
                        value: '最高难度'
                        }
                ],
            ways: [
                {value: '人工阅卷'},
                {value: '电脑阅卷'}
            ],
            // editorText: '直接初始化值', // 双向同步的变量
            // editorTextCopy: '',  // content-change 事件回掉改变的对象
            ruleForm: {
                    title: '',
                    adjunct: '',
                    content: '',
                    topicType: '单选题',
                    degree: '一般',
                    topic: '',
                    time: '',
                    correct: "A",
                    optionA: '',
                    optionB: '',
                    optionC: '',
                    optionD: '',
                    optionE: '',
                    optionF: '',
                    grade: '',
                    checkList: ['A'],
                    gap: '',
                    date: '',
                    way: ''
                },
            rules: {
                title: [
                    { required: true, message: '请输入标题', trigger: 'blur' },
                    { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
                ],
                adjunct: [
                    { required: true, message: '请选择或创建附件', trigger: 'blur' }
                ],
                time: [
                    { required: true, message: '请填写此卷考试时长', trigger: 'blur' }
                ],
                content: [
                    { required: true, message: '请输入题目详解', trigger: 'blur' }
                ],
                topic: [
                    { required: true, message: '请出题', trigger: 'blur' }
                ],
                optionA: [
                    { required: true, message: '请填写A选项内容', trigger: 'blur' }
                ],
                optionB: [
                    { required: true, message: '请填写B选项内容', trigger: 'blur' }
                ],
                optionC: [
                    { required: true, message: '请填写C选项内容', trigger: 'blur' }
                ],
                optionD: [
                    { required: true, message: '请填写D选项内容', trigger: 'blur' }
                ],
                optionE: [
                    { message: '请填写E选项内容', trigger: 'blur' }
                ],
                optionF: [
                    { message: '请填写F选项内容', trigger: 'blur' }
                ],
                grade: [
                    { required: true, message: '请填写此题分数', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        // editor组件事件监听
        _getMess(){
            let content = tinymce.get(this.tinymce).getContent()
            content = content.replace(/<[^>]+>/g,'')
            return content
        },
        // 最终提交
        submitTure(){
            if(!this.ruleForm.date){
                this.$message({
                    message: '请选择考试日期'
                })
                return
            }
            if(!this.ruleForm.way){
                this.$message({
                    message: '请选择阅卷方式'
                })
                return
            }
            // 在这里请求接口 失败不要删除
            const data = this.ruleForm;
            console.log(data)
            this.dialogFormVisible = false
            this.$message({
                message: '提交成功！您可以继续出题',
                type: 'success'
            })
            this.ruleForm = { title: '', adjunct: '', time: '', content: '', topicType: '单选题', degree: '一般',
                              topic: '', correct: "A", optionA: '', optionB: '', optionC: '', 
                              optionE: '', optionF: '',grade: '', checkList: ['A'] };
            this.tinymceHtml = '请输入内容'
        },
        // 判断提交
        submitForm(formName) {
            const pandect = this._getMess();
            if(!pandect || pandect == '请输入内容'){
                this.$message({
                    message: '请填写考试大纲',
                    type: 'error'
                })
                return
            }
            this.ruleForm.tinymceMess = pandect; //将考试大纲加进去
            this.$refs[formName].validate((valid) => {
            if (valid) {
                this.dialogFormVisible = true
            } else {
                return false;
            }
            });
        },
    },
    mounted(){
        // tinymce.init(this.init)
    }
}
</script>