<template>
    <div class="wrapper-login">
        <div class="login">
            <p class="title-login">用户登录</p>
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="username">
                    <span class="iconfont">&#xe613;</span>
                    <el-input placeholder="请输入用户名" type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pass">
                    <span class="iconfont">&#xe65e;</span>
                    <el-input placeholder="请输入密码" type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                </el-form-item>
                <div class="mess-box">
                    <el-checkbox v-model="checked">记住密码</el-checkbox>
                    <p>忘记密码？</p>
                </div>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">登 录</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<style scoped>
    .wrapper-login{
        position: relative;
        width: 100%;
        height: 100%;
        background: url('../assets/img/loginBg.png') no-repeat;
        background-position: center;
        background-size: cover;
        text-align: center;
        
    }
    .wrapper-login .login{
        position: absolute;
        top: 25%;
        left: 50%;
        transform: translate(-50%,0);
        width: 28vw;
        height: 50vh;
        background: #fff;
        border-radius: 8px
    }
    .wrapper-login .login .title-login{
        font-size: 15px;
        font-weight: 600;
        margin: 5vh 0 4vh;
    }
    .wrapper-login .login .mess-box{
        width: 70%;
        margin: auto;
        display: flex;
        justify-content: space-between;
        font-size: 14px;
    }
    .wrapper-login .login .el-form-item__content .el-button{
        width: 100%;
        margin-top: 5vh
    }
    
</style>

<script>
export default {
    data(){
        var validateUsername = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入用户名'));
            } else {
                callback();
            }
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                username: '',
                pass: ''
            },
            rules: {
                username: [
                    { validator: validateUsername, trigger: 'blur' }
                ],
                pass: [
                    { validator: validatePass, trigger: 'blur' }
                ]
            },
            checked: false,
        };
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
            if (valid) {
                // console.log(this.ruleForm)
                // if(this.checked){

                // }
                this.$router.push('/index')
                this.$message({
                    message: '登录成功',
                    type: 'success'
                })
            } else {
                console.log('error submit!!');
                return false;
            }
            });
        },
    },
    created(){
    }
}
</script>